.Toast {
  position: fixed;
  bottom: 1em;
  right: 1em;
  min-width: 225px;
  padding: 0.75em 1em;
  background-color: #000;
  color: #fff;
  font-size: 14px;
  -webkit-transform: translateY(calc(100% + 1em));
      -ms-transform: translateY(calc(100% + 1em));
          transform: translateY(calc(100% + 1em));
  -webkit-transition: -webkit-transform 225ms 100ms cubic-bezier(0.0, 0.0, 0.2, 1);
  transition: -webkit-transform 225ms 100ms cubic-bezier(0.0, 0.0, 0.2, 1);
  -o-transition: transform 225ms 100ms cubic-bezier(0.0, 0.0, 0.2, 1);
  transition: transform 225ms 100ms cubic-bezier(0.0, 0.0, 0.2, 1);
  transition: transform 225ms 100ms cubic-bezier(0.0, 0.0, 0.2, 1), -webkit-transform 225ms 100ms cubic-bezier(0.0, 0.0, 0.2, 1);
  will-change: transform;
  opacity: 0;
}

.Toast--show {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.Toast--error {
  background-color: #f30d37;
  color: #fff;
}

.Toast--success {
  background-color: #00c561;
  color: #fff;
}

.Toast--info {
  background-color: #26448f;
  color: #fff;
}



